<template>
  <div class="login">
    <section class="hero">
      <div class="hero-body">
        <div class="columns">
          <div class="column card is-half">
            <div class="card-content">
              <div class="content">
                <div class="columns">
                  <div class="column">
                    <div class="row">Issue Date:</div>
                    <div class="row">Issue No:</div>
                    <div class="row">Issue Priority:</div>
                    <div class="row">Issue Reporter:</div>
                    <!-- <div class="row">Responder:</div> -->
                  </div>
                  <div class="column">
                    <div class="row">
                      {{ call.createdAt | formatDate }}
                    </div>
                    <div class="row">
                      {{ call.id }}
                    </div>
                    <div class="row">
                      {{ call.priority }}
                    </div>
                    <div class="row">
                      {{
                        call.user_creator.name + " " + call.user_creator.surname
                      }}
                    </div>
                    <!-- <div class="row" v-if="call.user_solver != null">
                      {{
                        call.user_solver.name + " " + call.user_solver.surname
                      }}
                    </div>
                    <div class="row" v-else>-</div> -->
                  </div>
                  <div class="column">
                    <div class="row">Update Date:</div>
                    <div class="row">Issue Type:</div>
                    <div class="row">Category:</div>
                    <!-- <div class="row">Ulalacak Telefon:</div> -->
                    <div class="row">Responder:</div>
                  </div>
                  <div class="column">
                    <div class="row">
                      {{ call.updatedAt | formatDate }}
                    </div>
                    <div class="row">
                      {{ call.call_type }}
                    </div>
                    <div class="row">
                      {{ call.category.title }}
                    </div>
                    <!-- <div class="row">
                      <span
                        class="is-align-items-center is-flex"
                        v-if="
                          call.user_creator != null &&
                          call.user_creator.phone != null
                        "
                      >
                        {{ call.user_creator.phone }}
                      </span>
                      <span v-else>-</span>
                    </div> -->
                    <div class="row">
                      <span
                        class="is-align-items-center is-flex"
                        v-if="
                          call.status[call.status.length - 1] != null &&
                          call.status[call.status.length - 1].to != null
                        "
                      >
                        {{
                          call.status[call.status.length - 1].to.name +
                          " " +
                          call.status[call.status.length - 1].to.surname
                        }}
                      </span>
                    </div>
                  </div>
                </div>
                <!-- {{ call._id }}
                <a href="#">@bulmaio</a>. <a href="#">#css</a>
                <a href="#">#responsive</a>
                <br /> -->
              </div>
            </div>
            <footer class="card-footer">
              <v-btn
                @click="showAssign = true"
                class="card-footer-item has-background-link has-text-link-light"
                >Assign Issue</v-btn
              >
              <v-btn
                @click="showRespond = true"
                class="card-footer-item has-background-danger has-text-link-light"
                >Update Issue</v-btn
              >
            </footer>
          </div>
          <div class="column card is-half">
            <div class="card-title">Issue Description</div>
            <div class="card-content">
              <div class="content">
                <div class="columns">
                  <div class="column is-full pr-3">
                    <p class="is-multiline" style="overflow: auto;">{{ call.description }}</p>
                  </div>
               
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    <MyModal
      name="Assign Responder"
      :item="call"
      title="Assign Responder"
      btnSucess="Assign"
      v-on:childToParent="onChildClick"
      btnCancel="sdads"
      :usersx="users"
      :isShowModal="show"
    >
      asd
    </MyModal>
    <MyModalRespond
      name="Update Issue"
      :item="call"
      title="Update Issue"
      btnSucess="Assign"
      v-on:childToParent="onChildClick"
      btnCancel="sdads"
      :usersx="users"
      :isShowModal="showRespond"
    >
      asd
    </MyModalRespond>
    <MyModalA
      name="Assign Responder"
      :item="call"
      title="Assign Responder"
      btnSucess="Assign"
      v-on:childToParent="onChildClick"
      btnCancel="sdads"
      :usersx="users"
      :isShowModal="showAssign"
    >
      asd
    </MyModalA>
    <!-- <ResponseText v-if="isRedirecting" :result="this.response" /> -->
    <section class="hero">
      <div class="hero-body">
        <div class="is-fullwidth">
          <v-card>
            <v-card-title>
              <!-- {{ call.status }} -->
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
              ></v-text-field>
            </v-card-title>
            <v-data-table
              class="is-fullwidth"
              :sort-by.sync="columnName"
              :sort-desc.sync="isDescending"
              :items="call.status"
              :headers="headers"
              :search="search"
            >
              <template v-slot:item="row">
                <tr>
                  <td class="is-left">{{ row.item.statusType }}</td>
                  <td class="is-left">{{ row.item.aciklama }}</td>
                  <td>{{ row.item.to.name + " " + row.item.to.surname }}</td>
                  <td>{{ row.item.createdAt | formatDate }}</td>
                </tr>
              </template>
            </v-data-table>
          </v-card>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import axios from "axios";
import MyModal from "../../components/MyModal";
import MyModalA from "../../components/MyModalA";
import MyModalRespond from "../../components/MyModalRespond";
import Vue from "vue";
// usxers =[];
// async function getUsers() {
//   await axios
//     .get("http://127.0.0.1:3000/api/users")
//     .then((response) => {
//       console.log(response.data);
//       this.users = response.data;
//     })
//     .catch((error) => console.log(error));
// }
export default {
  components: { MyModal, MyModalRespond, MyModalA },

  data() {
    return {
      users: [],
      selectedItem: undefined,
      show: false,
      call: undefined,
      showRespond: false,
      showAssign: false,
      search: "",
      columnName: "id",
      isDescending: true,
      headers: [
        { text: "Issue Status", align: "center", value: "statusType" },
        { text: "Respond", align: "center", value: "createdAt" },
        { text: "Responder", align: "center", value: "createdAt" },
        {
          text: "Update Date",
          align: "center",
          value: "to.name",
        },
      ],
    };
  },

  async beforeCreate() {},
  async created() {
    // fetch the data when the view is created and the data is
    // already being observed

    await axios
      .get(Vue.prototype.$url + "/api/users")
      .then((response) => {
        console.log("response");
        console.log(response.data);
        this.users = response.data;
        console.log("ata");
        // this.setUsers);
      })
      .catch((error) => console.log(error));
    console.log("assss");
    this.getCall();
  },
  methods: {
    setUsers(value) {
      this.$emit("users", value);
    },
    onChildClick(value) {
      this.show = value;
      this.showAssign = value;
      this.showRespond = value;
      this.getCall();
    },
    onButtonClick(item) {
      console.log("click on " + item.id);
    },

    getCall() {
      var id = this.$route.params.callId;
      console.log("getting");
      axios
        .get(Vue.prototype.$url + "/api/calls/" + id)
        .then((response) => {
          console.log(response.data);
          this.call = response.data;
          //   if (response.data.token) {
          //     this.calls = "Giri baarl ynlendiriliyorsunuz";
          //   }
        })
        .catch((error) => console.log(error));
    },
  },
};
// getUsers();
</script>
<style scoped>
td {
  vertical-align: middle;
}
</style>